<template>
	<view class="container">
		<view class="header">
			<!-- <image src="/static/images/index-bg@3x.png" class="bg-title"></image> -->
			<view class="header-title-common">
				<p @click="back" />
				<b>会员中心</b>
				<a v-if="number>0"  @click="goguanli">激活码管理</a>
			</view>
		</view>
		<view class="card-box" >
			<image src="/static/viplogin.png" class="bg"></image>
			<view class="info">
				<image src="/static/head.jpg" class="icon"></image>
				<p>{{fullname}}</p>
				<b v-if="vipLevel==0" style="font-size: 20upx;wid 60upx;padding-left: 10upx;padding-right: 10upx;">普通</b>
				<b v-else  style="font-size: 20upx;wid 60upx;padding-left: 10upx;padding-right: 10upx;">钻石{{vipLevel}}</b>
			</view>
			
		</view>
		<!-- <image src="/static/huodong.png" class="bg"></image> -->
		<view class="main-box">
			<h1>
				<p>开通方式</p>
				<a>付费100或自用还款20万</a>
			</h1>
			<view class="pay-box"  >
				<h2 v-if="type==1"   style="border: 1upx solid #46c7a9;color: #46c7a9;">
					<p>支付宝</p>
					<p style="margin-top: 30upx;">￥100</p>
				</h2>
				<!-- <h2 v-else  @click="type=1" style="border: 1upx solid #F2EECF;">
					<p>支付宝</p>
					<p style="margin-top: 30upx; ">￥100</p>
				</h2> -->
				<!-- <h2 v-if="type==2"   style="border: 1upx solid #F0AD4E;">
					<p>激活码</p>
					<p style="margin-top: 30upx;"></p>
				</h2>
				<h2 v-else  @click="type=2" style="border: 1upx solid #F2EECF;">
					<p>激活码</p>
					<p style="margin-top: 30upx; "></p>
				</h2> -->
<!-- 				<h2 v-if="type==2" style="border: 1upx solid #F0AD4E;">
					<p>激活码</p>
					<p>支付宝</p>
				</h2>
				<h2 v-if="type==1"  @click="type=2" style="border: 1upx solid #F2EECF;">>
					<p>激活码</p>
					<p>支付宝</p>
				</h2> -->
			</view>
	
		<view class="tips"  v-if="type==1">
			<p  style="font-size: 20upx;">钻石1特权：</p>
			<p  style="font-size: 20upx;">收款手续降低，还款每万元省10元</p>
			<p  style="font-size: 20upx;">推荐好友开启管理收益每万元赚10元</p>
		</view>
		<view class="tips"  v-else>
			<input  type="text" v-model="vipKey" placeholder="请输入激活码" />
			<p  style="font-size: 20upx;"></p>
			<p  style="font-size: 20upx;"></p>
		</view>

				<h3 @click="paykey">立即开通</h3>
				<h4  @click="goqunyi">查看权益</h4>
		</view>
		

	</view>
</template>

<script>
	export default {
		data() {
			return {
				type:1,
				vipKey:"",
				number:0,
				fullname:"",
				vipLevel:0,
			};
		},
		onShow() {
			this.getkey()
			this.api_getUser()
		},
		methods:{
			
			 //支付宝支付页
			   		zfbsub(id) {
			   			uni.request({
			   			 
			   			    url: URL.baseUrl + 'auctionBuy/wapPay?auctionPaymentId=' + id, //支付ID
			   			  	method:'POST',
			   			    data: {
			   			        
			   			    },
			   			    header: {
			   			        'token': uni.getStorageSync("token")
			   			    },
			   			    success: (res) => {
			   			        //将接口返回的Form表单显示到页面
			   			        document.querySelector('body').innerHTML = res.data;
			   			       //调用submit 方法
			   			        document.forms[0].submit()
			   			       
			   			    }
			   			});
			   		 
			   		},
			goguanli(){
				uni.navigateTo({
					url:"activate"
				})
			},
			goqunyi(){
				uni.navigateTo({
					url:"/pages/users/revenueRule"
				})
			},
			//获取激活码/api/user/key/get/count
			getkey(){
				let t = this
				let body = {}
				this.$meapi.api_keyGetCount(body).then(res=>{
					// console.log("res.data=",res.data)
					if(res.code==0){
						this.number = res.data
					}else{
						uni.showToast({
							title:res.message,
							icon:"none"
						})
					}
				
				}).catch(err=>{
					console.log("api_changeAlipay失败",err)
					uni.showToast({
						title:"网络链接失败",
						icon:"none"
					})
				})
			},
			back(){
				uni.navigateBack({
					
				})
			},
			//获取个人资料
				api_getUser(){
					let t = this
					let body = {
						id:uni.getStorageSync("users").id
					}
					
					this.$meapi.api_getUser(body).then(res=>{
						console.log("api_getUser成功",res.data.vipLevel)
						if(res.code==0){
								t.fullname = res.data.fullname.substring(0,1)+"*"+res.data.fullname.substring(2,res.data.fullname.length)
								t.vipLevel=res.data.vipLevel
						}else if(res.code == 5){
							uni.showModal({
								title:"未登录",
								content:"立即去登录",
								success(res) {
									if(res.confirm){
										uni.reLaunch({
											url:"/pages/login/login"
										})
									}
								}
							})
						}
					
					}).catch(err=>{
						console.log("api_changeAlipay失败",err)
						uni.showToast({
							title:"网络链接失败",
							icon:"none"
						})
					})
				},
			//使用激活码   非json/api/user/key/use
			paykey(){
				let t = this
				if(t.type==1){
					uni.showToast({
						title:"暂未开通",
						icon:"none"
					})
					return
				}
				if(t.vipKey==""){
					uni.showToast({
						title:"请输入激活码",
						icon:"none"
					})
					return
				}
				let body = {
					key:t.vipKey
				}
				this.$meapi.api_vipuserKey(body).then(res=>{
					
					if(res.code==0){
						t.api_getUser()
						uni.showToast({
							title:"激活成功",
						})
					}else{
						uni.showToast({
							title:res.message,
							icon:"none"
						})
					}
				
				}).catch(err=>{
					console.log("api_changeAlipay失败",err)
					uni.showToast({
						title:"网络链接失败",
						icon:"none"
					})
				})
			},
			

		}
	}
</script>

<style lang="scss">
	.bg-title {
		width: 100%;
		height: 400upx;
		position: absolute;
		left: 0;
		top: 0;
		z-index: -1;
	}


	.header {
		width: 100%;
		position: relative;
		padding: 30upx;
		padding-top: calc(var(--status-bar-height) + 30upx);
		min-height: 100upx;
		background-color: #46c7a9;
		.header-title-common {
			display: flex;
			align-items: center;
			width: 100%;
			justify-content: center;
			position: relative;
			// #ifdef H5
			margin-top: 50upx;
			// #endif
			a {
				position: absolute;
				font-size: 22upx;
				right: 0;
				color: white;
			}

			b {
				font-size: 40upx;
				font-family: PingFang SC;
				font-weight: bold;
				color: white;
			}

			p {
				transform: rotate(45deg);
				border-bottom: 5upx solid white;
				border-left: 5upx solid white;
				width: 20upx;
				height: 20upx;
				position: absolute;
				left: 0;
			}
		}
	}


	.card-box {
		width: 100%;
		height: 200upx;
		position: relative;
		padding: 40upx 60upx 70upx;
		display: flex;
		align-items: center;
		margin-bottom: 200upx;
		background-color: #46c7a9;
		.bg {
			width: 620upx;
			height: 364upx;
			position: absolute;
			left: 50%;
			// border-radius: 20upx;
			margin-left: -310upx;
			bottom: -182upx;
			z-index: 2;
			
			
		}
		.info{
			position: absolute;
			z-index: 3;
			width: 60%;
			display: flex;
			left: 20%;
			padding-top: 160upx;
			.icon {
				width: 90upx;
				height: 90upx;
				border-radius: 50%;
				margin-right: 20upx;
			}

			p {
				font-size: 24upx;
				font-family: PingFang SC;
				font-weight: bold;
				line-height: 80upx;
				color: white;
			}

			b {
				margin-left: auto;
				// width: 51upx;
				height: 51upx;
				background: #BC7631;
				border-radius: 20upx;
				color: white;
				font-weight: normal;
				display: flex;
				align-items: center;
				justify-content: center;
				margin-top: 20upx;
			}
		}
		
	}
	.main-box{
		// background-color: #2E2F37;
		min-height: 80vh ;
		padding: 40upx;
		h1{
			font-size: 36upx;
			font-family: PingFang SC;
			font-weight: bold;
			// color: #F7CF9E;
			p{
				position: relative;
			}
			p::after{
				content: '';
				width: 100%;
				height: 3upx;
				display: block;
				position: absolute;
				top: 20upx;
				left: 170upx;
				background-color: #312417;
			}
			a{
				font-size: 20upx;
				font-family: PingFang SC;
				font-weight: 500;
				// color: #F7CF9E;
			}
		}
		.pay-box{
			display: flex;
			align-items: center;
			width: 550upx;
			margin: 70upx auto 30upx;
			justify-content: space-between;
			h2{
				width: 640upx;
				height: 166upx;
				border-radius: 20px;
				// border: 1upx solid #F2EECF;
				text-align: center;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				p{
					font-size: 30upx;
					font-family: PingFang SC;
					font-weight: bold;
					// color: #F2EECF;
				}
			}
			
		}
		input{
			border: 3upx solid #F2EECF;
			font-size: 24upx;
			font-family: PingFang SC;
			font-weight: 500;
			color: #F2EECF;
			padding: 0 40upx;
			width: 550upx;
			height: 70upx;
			// margin: 50upx auto;
			border-radius: 10upx;
		}
		h3{
			height: 86upx;
			width: 550upx;
			margin: 0 auto;
			// background: linear-gradient(269deg, #D79F65 0%, #FDD8AA 100%);
			background-color: #46c7a9;
			display: flex;
			align-items: center;
			justify-content: center;
			font-size: 30upx;
			font-family: PingFang SC;
			font-weight: bold;
			color: white;
			border-radius: 20upx;
		}
		h4{
			font-size: 24upx;
			font-family: PingFang SC;
			font-weight: 500;
			// color: #F6D58B;
			display: flex;
			justify-content: center;
			margin: 40upx 0;
		}
		.tips{
			font-size: 24upx;
			font-family: PingFang SC;
			font-weight: 500;
			// color: #F6D58B;
			// margin: 40upx auto;
			margin-left: 65upx;
			width: 550upx;
			height: 200upx;
			// background-color: red;
